"use client";
import { useEffect, useState } from "react";

export default function FloatingAlert({
  type = "info",
  message,
  duration = 0,
}) {
  const [visible, setVisible] = useState(true);

  /*useEffect(() => {
    if (duration > 0) {
      const timer = setTimeout(() => setVisible(false), duration);
      return () => clearTimeout(timer);
    }
  }, [duration]);*/

  if (!visible) return null;

  const getAlertStyles = () => {
    switch (type) {
      case "error":
        return "bg-red-500 text-white";
      case "success":
        return "bg-green-500 text-white";
      case "warning":
        return "bg-yellow-500 text-black";
      default:
        return "bg-blue-500 text-white";
    }
  };

  return (
    <div className="fixed inset-0 flex justify-center items-center">
      <div
        className={`p-2 rounded-md shadow-lg transition-opacity duration-300 ease-in-out ${getAlertStyles()}`}
      >
        <div className="flex justify-between items-center">
          <span>{message}</span>
          <button
            onClick={() => setVisible(false)}
            className="ml-4 text-xl font-bold opacity-80 hover:opacity-100"
          >
            &times;
          </button>
        </div>
      </div>
    </div>
  );
}



"use client";
import { useState, useEffect } from "react";
import { subscribeToAlerts } from "@/app/utils/alertEmitter";

export default function FloatingAlert() {
  const [alert, setAlert] = useState(null);

  useEffect(() => {
    const unsubscribe = subscribeToAlerts((newAlert) => {
      setAlert(newAlert);
      if (newAlert.duration > 0) {
        setTimeout(() => setAlert(null), newAlert.duration);
      }
    });

    return () => unsubscribe(); // Cleanup on unmount
  }, []);

  if (!alert) return null;

  const getAlertStyles = () => {
    switch (alert.type) {
      case "error":
        return "bg-red-500 text-white";
      case "success":
        return "bg-green-500 text-white";
      case "warning":
        return "bg-yellow-500 text-black";
      default:
        return "bg-blue-500 text-white";
    }
  };

  return (
    <div className="fixed inset-0 flex justify-center items-center">
    <div className="fixed top-5 left-1/2 transform -translate-x-1/2 p-4 rounded-md shadow-lg transition-opacity duration-300 ease-in-out z-50">
      <div className={`p-3 rounded-md shadow-lg flex items-center justify-between ${getAlertStyles()}`}>
        <span>{alert.message}</span>
        <button onClick={() => setAlert(null)} className="ml-4 text-xl font-bold opacity-80 hover:opacity-100">
          &times;
        </button>
      </div>
    </div>
    </div>
  );
}